*{
  box-sizing: border-box;
}

body{
  background-color: $clr1;
}
.app{
  height: 100vh;
}
h1{
  font-size: 3rem;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
  font-family:poppins-bold,poppins-black;
}
p{
  margin: 0;
  padding: 0;
  font-family: open-sans-italic;
}

ul{
  margin: 0;
  padding: 0;
  list-style: none;
}

button{
  cursor: pointer;
}
a{
  text-decoration: none;
  font-family: open-sans-italic;
}

select{
  padding: 10px;
  outline: none;
  border: none;
  border-radius: 10px;
  background-color: $clr2;
  color:$clr1;
  font-size: 1.1em;
  font-family: open-sans-italic;

}

