.product-filter{
    display: grid;
    grid-template-columns: auto;
    grid-auto-flow: column;
    justify-content: center;
    align-items: baseline;
    gap: 20px;
    
}

@include for-small-layout{
    .product-filter{
        grid-auto-flow: row;
        justify-content: flex-start;
        // justify-items: flex-start;
    }
}