.layout{
    display: grid;
    grid-template-columns: 40px 1fr 40px;
    grid-auto-rows: max-content;
    gap: 20px;
    >*{
        grid-column: 2;
    }
}

@include for-mobile-layout{
    .layout{
        grid-template-columns: 1fr;
        margin: 15px;
        >*{
            grid-column: 1;
        }
    }
}