.app-header {
  margin: 15px 5px 0;
  display: flex;
  justify-content: space-between;
  .logo{
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 700;
      border-radius: 2rem;
      padding: 10px;
      background-color: $clr2;
      color:$clr1;
      cursor: pointer;
  }
  .app-nav {
    display: flex;
    flex-basis: 10%;
    gap: 20px;
    a {
        border-radius: 3rem;
        font-size: 1.3rem;
        padding: 10px;
        width: 120px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: background-color color outline 0.3s;
        color: $clr2;
      &:hover {
        background-color: rgba($clr2,0.1);
        color:$clr2;
      }
      &.active-nav {
        background-color:$clr2;
        color: $clr1;
      }
      &:active{
          outline: 2px solid;
      }
    }
  }
}
