.product-preview{
    height: 100%;
    width: 100%;
    display: flex;
   flex-wrap: wrap;
    gap: 10px;
    padding: 10px;
    border-radius: 10px;
    color:$clr2;
    transition: background-color color 0.3s;
    >*{
       flex-basis: 100%;
        word-wrap: break-word;
    }
    img{
        width: 100%;
        height: 200px;
        object-fit: contain;
        object-position: left;
        border-radius: 10px;
    }
    &:hover{
        background-color: rgba($clr2,0.1);
        
        cursor: pointer;
    }
}